// import {getTemplateTimeString} from "../tool.js";
import Toast from '@/wxcomponents/vant/dist/toast/toast';

export default {
	props:["isShow","resetValue","resetIndex","isAddorReset"],
	data() {
		return {
			value: '',
			show: false,
			minDate:new Date().getTime(),
			maxDate:new Date(2025,10,1).getTime(),
			currentDate:new Date().getTime(),
		}
	},
	// watch:{
	// 	 currentDate(newVal){
	// 		this.currentDate =  getTemplateTimeString(newVal,'yyyy/MM/dd hh:mm');
	// 	}
	// },
	methods: {
		changeChildVal(){
			console.log(this.resetValue)
			this.value = this.resetValue
		},
		getValue() {
			console.log(this.value)
		},
		changeVal(e) {
			this.value = e.detail;
			console.log(this.value)
		},
		confirm(){
			console.log(this.resetValue)
			let thing = {
				text:this.value,
				desc:this.currentDate,
				isComplete: false,
				isDel: false
			};
			if(this.value == ""){
				Toast({
					message:'添加的内容或时间不能为空！',
					selector: '#van-toast',
					context:this
				});
				// Toast('添加内容或时间不能为空');
				return;
			}
			this.$emit("addThing",thing);
			this.value = ""
		},
		reset(){
			let obj = {
				text:this.value,
				desc:this.currentDate,
				isComplete: false,
				isDel: false
			}
			this.$emit("resetFinish",{index:this.resetIndex,obj:obj})
			this.value = "";
		},
		cancel() {
			this.$emit("isShow");
			this.value = ""
		},
		showPopup() {
			console.log(this.show)
			this.show = true;
		},
		onClose() {
			console.log(this.show)
			this.show = false;
		},
	    onInput(e){
			let time = e.detail;
			this.currentDate = time ;
			// this.currentDate = getTemplateTimeString(time,'yyyy/MM/dd hh:mm');
		}
	}
}
